import axios from 'axios';





    axios.defaults.baseURL ="https://cdn.jsdelivr.net/gh/parsevip/Pictures@1.0.3"
    axios.defaults.timeout = 30000
    axios.interceptors.request.use(
    config => {
        if (localStorage.getItem('user')) {
            config.headers.Authorization ='Bearer '+JSON.parse(localStorage.getItem('user')).token;
        }
        return config;
    },
    error => {
        return Promise.reject(error);
    });

// post请求
let postRequest=function(url, data = {}, ContentType = 'x-www-form-urlencoded') {
    return new Promise((resolve, reject) => {
        axios({
            url: url,
            method:'post',
            headers: {
                'Content-Type': 'application/'+ContentType
            },
            data: data
        })
            .then(res => {
                if(res.status===200){
                    if(res.data.code==401){
                       // Message.warning('认证失效，请重新登陆！');
                     //   router.push('/login');
                    }
                    else if(res.data.code==200){
                        resolve(res.data)
                    }
                    else {
                      //  Message.warning(res.data.msg);
                    }

                }
                else if(res.status===404){
                  //  console.log('接口地址错误');
                }
            })
            .catch(res => {
               // Message.error('服务器内部错误！');
                reject(res)
            })

    })
}

let getRequest=function(url, data = {}, ContentType = 'x-www-form-urlencoded') {
    return new Promise((resolve, reject) => {
        axios({
            url: url,
            method: 'get',
            headers: {
                'Content-Type': 'application/'+ContentType
            },
            params: data
        })
            .then(res => {
                if(res.status===200){
                    if(res.data.code==401){
                      //  Message.warning('认证失效，请重新登陆！');
                      //  router.push('/login');
                    }
                    else if(res.data.code==200){
                        resolve(res.data)
                    }
                    else {
                       // Message.warning(res.data.msg);
                    }
                }
                else if(res.status===404){
                 //   console.log('接口地址错误');
                }
            })
            .catch(res => {
              //  Message.error('服务器内部错误！');
                reject(res);
            })

    })
}



let getExcelDownload=function(url, data = {},fileName="导出文件", ContentType = 'x-www-form-urlencoded') {
    return new Promise((resolve, reject) => {
        axios({
            url: url,
            method:'post',
            headers: {
                'Content-Type': 'application/'+ContentType
            },
            responseType: 'blob',
            data: data
        })
            .then(res => {
                if(res.status===200){
                  //  const content = res;
                    const blob = new Blob([res.data], {type: 'application/ms-excel'});
                   // const fileName = '测试表格123.xls';
                    if ('download' in document.createElement('a')) { // 非IE下载
                        const elink = document.createElement('a');
                        elink.download = fileName+'.xls';
                        elink.style.display = 'none';
                        elink.href = URL.createObjectURL(blob);
                        document.body.appendChild(elink);
                        elink.click();
                        URL.revokeObjectURL(elink.href); // 释放URL 对象
                        document.body.removeChild(elink);
                    } else { // IE10+下载
                        navigator.msSaveBlob(blob, fileName);
                    }
                    resolve(res.data)
                }
                else if(res.status===404){
                 //   console.log('接口地址错误');
                }
            })
            .catch(res => {
              //  Message.error('服务器内部错误！');
                reject(res)
            })

    })

}


export default {
    postRequest: postRequest,
    getRequest:  getRequest,
    getExcelDownload:getExcelDownload
}
