<template>

    <div id="b">
      <!--  <van-dialog v-model="show" title="我的课程表" message="sss" >

        </van-dialog>-->

        <van-popup v-model="show"  closeable round close-icon="close" :style="{ height: '80%',width: '90%' }">
            <img :src="share_url" width="100%" id="share">
        </van-popup>

    <div id="coursesTable">



    </div>

        <van-divider dashed>长按识别或者扫描二维码</van-divider>
        <img src="https://i.loli.net/2019/12/31/pIP8HCjlDXvGJfB.png" width="100%" height="100%"/>

    </div>


</template>

<script>
    import html2canvas from "html2canvas";
    import Timetables from 'timetables';

    import Vue from 'vue';
    import { Toast } from 'vant';

    Vue.use(Toast);


    // eslint-disable-next-line no-unused-vars
    let Timetable;

    export default {
        name: "Grid",
        data() {
            return {
                share_url:'',
                fileList: [
                    { url: 'https://img.yzcdn.cn/vant/leaf.jpg' }
                ],
                show:false,
                value: 4,
                items:['1','2','3','4','5','6','7','8'],
                images: [
                    'https://img.yzcdn.cn/vant/apple-1.jpg',
                    'https://img.yzcdn.cn/vant/apple-2.jpg',
                    'https://cdn.jsdelivr.net/gh/parsevip/Pictures/images/640.webp.jpg'
                ],
                active: 'home',
                courseList: [
                    ['大学英语(Ⅳ)@10203','C语言','','','','','毛概@14208','毛概@14208','','','','选修'],
                    ['','','信号与系统@11302','信号与系统@11302','模拟电子技术基础@16204','模拟电子技术基础@16204','','','','','',''],
                    ['大学体育','大学体育','形势与政策(Ⅳ)@15208','形势与政策(Ⅳ)@15208','','','电路、信号与系统实验','电路、信号与系统实验','','','',''],
                    ['','','','','电装实习@11301','电装实习@11301','','','','大学体育','大学体育',''],
                    ['','','数据结构与算法分析','数据结构与算法分析','','','','','信号与系统','信号与系统','',''],
                ],
                el: '#coursesTable',
                week: ['一\n12.30', '二', '三', '四', '五'],
                timetableType: [
                    [{index: '1',name: '8:30'}, 1],
                    [{index: '2',name: '9:30'}, 1],
                    [{index: '3',name: '10:30'}, 1],
                    [{index: '4',name: '11:30'}, 1],
                    [{index: '5',name: '12:30'}, 1],
                    [{index: '6',name: '14:30'}, 1],
                    [{index: '7',name: '15:30'}, 1],
                    [{index: '8',name: '16:30'}, 1],
                    [{index: '9',name: '17:30'}, 1],
                    [{index: '10',name: '18:30'}, 1]
                ],
                highlightWeek : new Date().getDay(),
                styles : {
                    Gheight: 50,
                    leftHandWidth: 50,
                    palette:[]
                }
            }
        },
        methods:{
            createPicture() {
                html2canvas(document.getElementById('coursesTable'), {
                    onrendered: function(canvas) {
                        document.body.appendChild(canvas);
                    },
                    // width: 300,
                    // height: 300
                });
            },
            //下载图片
            fileDownload(downloadUrl) {
                let aLink = document.createElement("a");
                aLink.style.display = "none";
                aLink.href = downloadUrl;
                aLink.download = "监控详情.png";
                // 触发点击-然后移除
                document.body.appendChild(aLink);
                aLink.click();
                document.body.removeChild(aLink);
            },
            ss(e){
                alert(JSON.stringify(e));
            },
            getGrid: function () {
                this.$HttpClient.getRequest("/get.json").then(res => {
                    this.week=res.data.week;
                    res.data=res.data.list;









                    let da;
                    let aaa = res.data.length-3;
                    // eslint-disable-next-line no-console
                    console.log(aaa);
                    // eslint-disable-next-line no-console
                    console.log(JSON.stringify(res.data));

                    // eslint-disable-next-line no-unused-vars

                    var zz=new Array();
                    // eslint-disable-next-line no-console
                  //  console.log("z-->"+z.length);
                    // eslint-disable-next-line no-empty,no-unused-vars
                    var k=0;
                    // eslint-disable-next-line no-constant-condition
                    while(true) {
                        var z = new Array();
                        // eslint-disable-next-line no-console
                        console.log("初始化数组" + k);
                        for (let i = 2; i < aaa; i++) {

                            // eslint-disable-next-line no-empty
                            if (i % 2 == 0) {
                                da = res.data[i][k + 1];

                                if(da!=null && da.length===1){
                                    z.push(null);
                                }
                                else {
                                    z.push(da);
                                }
                            } else {
                                var old = res.data[i-1][k + 1];
                                da = res.data[i][k + 1];
                                if(da!=null && da.length>3){
                                    z.push(da);
                                }
                                else if(old!=null && old.length===1){
                                    z.push(null);
                                }
                                else {
                                    z.push(old);
                                }
                            }
                            if((i+1)==aaa){
                                k++;
                                zz.push(z);
                            }
                    }
                    if(k==5){
                        break;
                    }
                    }
                    // eslint-disable-next-line no-console
                    console.log("zzz==>"+zz[2]);
                    var thisa=this;
                    Timetable = new Timetables({
                        el: '#coursesTable',
                        timetables: zz,
                        week: this.week,
                        timetableType: this.timetableType,
                        highlightWeek: this.highlightWeek,
                        gridOnClick: function (e) {
                         //   let aa=this;
                            // eslint-disable-next-line no-undef
                            Toast.loading({
                                message: '截图生成中...',
                                forbidClick: true
                            });

                            var opts = {
                                useCORS: true // 【重要】开启跨域配置
                            };

                            html2canvas(document.querySelector("#b"),opts).then(canvas => {
                                let type="png";
                                let imgUrl =  canvas.toDataURL('image/' + type);
                                // eslint-disable-next-line no-console
                                console.log(imgUrl);
                                thisa.share_url=imgUrl;
                                thisa.show=true;

                                //    alert('截图成功')
                              //  document.body.appendChild(canvas);
                              //  document.getElementById("#coursesTable").append(canvas);
                            });


                            // eslint-disable-next-line no-console
                            console.log(e.name + '  ' + e.week +', 第' + e.index + '节课, 课长' + e.length +'节')
                        },
                        styles: this.styles
                    })

                })
            }
        },
        created(){
          //  alert('created');
            this.getGrid();
        },
        mounted(){
            // 实例化(初始化课表)


        }

    }
</script>

<style>

    #coursesTable {
     /*   padding: 15px 10px;*/
    }

    .Courses-head {
        background-color: #edffff;
    }

    .Courses-head > div {
        text-align: center;
        font-size: 14px;
        line-height: 28px;
    }

    .left-hand-TextDom, .Courses-head {
        background-color: #f2f6f7;
    }

    .Courses-leftHand {
        background-color: #f2f6f7;
        font-size: 12px;
    }

    .Courses-leftHand .left-hand-index {
        color: #9c9c9c;
        margin-bottom: 4px !important;
    }

    .Courses-leftHand .left-hand-name {
        color: #666;
    }

    .Courses-leftHand p {
        text-align: center;
        font-weight: 900;
    }

    .Courses-head > div {
        border-left: none !important;
    }

    .Courses-leftHand > div {
        padding-top: 5px;
        border-bottom: 1px dashed rgb(219, 219, 219);
    }

    .Courses-leftHand > div:last-child {
        border-bottom: none !important;
    }

    .left-hand-TextDom, .Courses-head {
        border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
    }

    .Courses-content > ul {
        border-bottom: 1px dashed rgb(219, 219, 219);
        box-sizing: border-box;
    }

    .Courses-content > ul:last-child {
        border-bottom: none !important;
    }

    .highlight-week {
        color: #02a9f5 !important;
    }

    .Courses-content li {
        text-align: center;
        color: #666666;
        font-size: 14px;
        line-height: 50px;
    }

    .Courses-content li span {
        padding: 6px 2px;
        box-sizing: border-box;
        line-height: 18px;
        border-radius: 4px;
        white-space: normal;
        word-break: break-all;
        cursor: pointer;
    }

    .grid-active {
        z-index: 9999;
    }

    .grid-active span {
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
    }

</style>
